/* App.css */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  scroll-behavior: smooth;
}

.app {
  text-align: center;
}

.header {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #282c34;
  padding: 20px;
  z-index: 1000;
  color: white;
}

.header h1 {
  margin: 0;
}

.header nav {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 10px;
}

.header nav a {
  color: white;
  text-decoration: none;
  font-size: 1.2em;
}

.header nav a:hover {
  text-decoration: underline;
}

.section {
  padding: 100px 20px;
  margin-top: 60px;
}

.home {
  background-color: #f4f4f4;
}

.education, .projects, .skills {
  background-color: #fff;
}

.experience {
  background-color: #f4f4f4;
}

ul {
  list-style: none;
  padding: 0;
}

.footer {
  padding: 20px;
  background-color: #282c34;
  color: white;
}

.footer .social-links {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 10px;
}

.footer .social-links a {
  color: white;
  text-decoration: none;
}

.footer .social-links a:hover {
  text-decoration: underline;
}
